.basket-container {
    display: flex;
    flex-direction: column;
    height: 100%;
    border-radius: 5px;
    background-color: white;
    width: 60%;
    padding: 10px;
    margin-left: 26px;
  }
  .product-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .button-and-count-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .item-total-price {
    display: flex;
    flex-direction: row-reverse;
    font-weight: 500;
    opacity: 0.7;
  }
  .item-cost {
    display: flex;
    flex-direction: row-reverse;
    font-weight: 500;
  }
  .bill-total-section {
    display: flex;
    flex-direction: column;
  }
  .bill-section {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .bottom-line {
    opacity: 0.3;
  }
  .plus-button {
    border: none;
    width: 28px;
    height: 33px;
    border-radius: 5px;
    background-color: #4f09f3f2;
    color: white;
    margin-left: 2px;
    margin-right: 10px;
    font-weight: bold;
    font-size: 19px;
    cursor: pointer;
  }
  .minus-button {
    border: 3px solid rgb(67, 78, 235);
    width: 28px;
    height: 33px;
    border-radius: 5px;
    margin-left: 10px;
    margin-right: 2px;
    font-weight: bold;
    font-size: 19px;
    cursor: pointer;
  }
  .item-saving {
    display: flex;
    flex-direction: row-reverse;
    color: red;
    font-weight: 500;
  }
  .cross-icon {
    height: 20px;
    width: 20px;
    margin-left: 14px;
    border-radius: 20px;
    padding: 7px;
    cursor: pointer;
  }
  .cross-icon:hover {
    background-color: #a0a0a0;
  }
  